import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import CustomSelect from '../components/utilities/CustomSelect';
import DocPreview from '../components/DocPreview';
import { IconContext  } from "react-icons";
import { IoAddCircleSharp, IoSearch } from 'react-icons/io5';

export default function UserDocuments() {
    const [tab, setTab] = useState('docs');

    return (
        <div className='box px-0 p-lg-4 p-xl-5'>
            <Link to="/personal-account" className='fs-12 fw-5 d-block mb-3 mb-sm-5'><span className='green fs-15 me-2'></span> </Link>
            <h1 className='dark-blue text-center d-lg-none'></h1>
            <div className='d-md-flex flex-row-reverse justify-content-between align-items-center mb-4 mb-xl-5'>
                <button type='button' data-bs-toggle="modal" data-bs-target="#docs-patterns" className='btn btn-2 fs-12 px-4 mb-4 mb-md-0'>
                    <IconContext.Provider value={{className: "icon-15 white", title: " " }}>
                        <IoAddCircleSharp />
                    </IconContext.Provider>
                    <span className='ms-2'> </span>
                </button>
                <div className='d-flex align-items-center fs-12 fw-5 title-font'>
                    <button type='button' className={(tab === 'docs') ? 'active tab-btn' : 'tab-btn'} onClick={()=>setTab('docs')}> </button>
                    <button type='button' className={(tab === 'patterns') ? 'active tab-btn ms-3 ms-sm-4 ms-xl-5' : 'tab-btn ms-3 ms-sm-4 ms-xl-5'} onClick={()=>setTab('patterns')}>  </button>
                </div>
            </div>

            <div className='d-md-flex align-items-center fs-11'>
                <form className='form-search'>
                    <input type="search" placeholder='  '/>
                    <button>
                        <IconContext.Provider value={{className: "icon-15 green", title: " " }}>
                            <IoSearch />
                        </IconContext.Provider>
                    </button>
                </form>
                <div className='d-flex justify-content-between justify-content-md-start align-items-center mt-3 mt-md-0 ms-md-5'>
                    <div className='d-flex align-items-center'>
                        <span className='d-none d-sm-block me-1'>&nbsp;:</span>
                        <CustomSelect name="docs-type" checkedOpt={1} options={['', ' 1', ' 2']} alignment="left"/>
                    </div>
                    <div className='d-flex align-items-center ms-md-5'>
                        <span className='d-none d-sm-block me-1'>:</span>
                        <CustomSelect name="date-sort" options={[' ', ' ']} alignment="left"/>
                    </div>
                </div>
            </div>

            {
                (tab === 'docs')?
                <div>
                    <div className='docs-header mt-3'>
                        <label>
                            <input type='checkbox' name='all'/>
                        </label>
                        <div className='title'></div>
                        <div className='number'></div>
                        <div className='date'> </div>
                        <div className='contractor'></div>
                        <div className="dropdown">
                        <button type='button'></button>
                        </div>
                    </div>
                    <DocPreview type='doc' className='mt-3' docId='id111' title='    ' number='689065980-67' date='13.12.2021' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                   <DocPreview type='doc' className='mt-3' docId='id111' title='' number='689065980-67' date='13.12.2021' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                   <DocPreview type='doc' className='mt-3' docId='id111' title='' number='689065980-67' date='13.12.2021' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                   <DocPreview type='doc' className='mt-3' docId='id111' title='' number='689065980-67' date='13.12.2021' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                </div>
                : <div>
                    <div className='docs-header mt-3'>
                        <label>
                            <input type='checkbox' name='all'/>
                        </label>
                        <div className='title'> </div>
                        <div className='doc-type'> </div>
                        <div className='contractor'></div>
                        <div className="dropdown">
                        <button type='button'></button>
                        </div>
                    </div>
                   <DocPreview type='pattern' className='mt-3' docId='id111' title='  ' note='-' docType='  ' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                   <DocPreview type='pattern' className='mt-3' docId='id111' title='  ' note='-' docType='  ' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                   <DocPreview type='pattern' className='mt-3' docId='id111' title='  ' note='-' docType='  ' contractor={{name:' ', url: '/personal-account/view-profile'}} />
                </div>
            }
        </div>
    )
}