import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import Pattern from '../components/Pattern';

export default function UserPatterns() {
    const [tab, setTab] = useState('cars');

    return (
        <div className='box px-0 p-lg-4 p-xl-5'>
            <Link to="/personal-account" className='fs-12 fw-5 d-block mb-3 mb-sm-5'><span className='green fs-15 me-2'></span> </Link>
            <h1 className='dark-blue text-center d-lg-none'></h1>
            <div className='d-flex align-items-center fs-12 fw-5 title-font mb-4 mb-xl-5'>
                <button type='button' className={(tab === 'cars') ? 'active tab-btn' : 'tab-btn'} onClick={()=>setTab('cars')}> (3)</button>
                <button type='button' className={(tab === 'cargo') ? 'active tab-btn ms-3 ms-sm-4 ms-xl-5' : 'tab-btn ms-3 ms-sm-4 ms-xl-5'} onClick={()=>setTab('cargo')}> (3)</button>
            </div>
            {
                (tab === 'cars')?
                <div>
                    <Pattern className='mb-3 mb-sm-4' type='car' title='--' note='' route=' +50  +50' date='' aboute=', , 20, 153, 13,6/2,45/2,45' payment=' . 80/, 76/,  50%' contacts='+ 7 (962) 458 65 79 '/>

                    <Pattern className='mb-3 mb-sm-4' type='car' title=' 1' note='' route=' +50  +50' date='' aboute=', , 20, 153, 13,6/2,45/2,45' payment=' . 80/, 76/,  50%' contacts='+ 7 (962) 458 65 79 '/>

                    <Pattern className='mb-3 mb-sm-4' type='car' title=' 2' note route=' +50  +50' date='' aboute=', , 20, 153, 13,6/2,45/2,45' payment=' . 80/, 76/,  50%' contacts='+ 7 (962) 458 65 79 '/>
                </div>
                : <div>
                    <Pattern className='mb-3 mb-sm-4' type='cargo' title='--' note='' route=' +50  +50' date='' aboute=', , 20, 153, 13,6/2,45/2,45' payment=' . 80/, 76/,  50%' contacts='+ 7 (962) 458 65 79 '/>

                    <Pattern className='mb-3 mb-sm-4' type='cargo' title=' 1' note='' route=' +50  +50' date='' aboute=', , 20, 153, 13,6/2,45/2,45' payment=' . 80/, 76/,  50%' contacts='+ 7 (962) 458 65 79 '/>

                    <Pattern className='mb-3 mb-sm-4' type='cargo' title=' 2' note route=' +50  +50' date='' aboute=', , 20, 153, 13,6/2,45/2,45' payment=' . 80/, 76/,  50%' contacts='+ 7 (962) 458 65 79 '/>
                </div>
            }
        </div>
    )
}