import React from 'react';
import { NavLink, Link } from 'react-router-dom';
import { IconContext  } from "react-icons";
import { MdLocalShipping } from 'react-icons/md';
import { IoCube, IoDocumentTextSharp, IoExitSharp, IoNewspaperSharp, IoAddCircleSharp, IoBriefcase } from 'react-icons/io5';
import { RiUserFill, RiMoneyDollarBoxFill } from "react-icons/ri";
import { BsFillHandIndexThumbFill } from "react-icons/bs";

export default function AccountMenu() {
    return (
        <div>
            <nav className="d-block d-lg-none mt-3 mb-4 mb-sm-5" aria-label="breadcrumb">
                <Link to="/" className="gray-3">&#10094;  </Link>
            </nav>
            <nav className="menu">
                <ul>
                    <li>
                        <NavLink to="profile">
                            <IconContext.Provider value={{className: "icon-15 green", title: "" }}>
                                <RiUserFill />
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'></span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="user-cars">
                            <IconContext.Provider value={{className: "icon-15 green", title: " " }}>
                                <MdLocalShipping />
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'> </span>
                        </NavLink>
                        <Link to="/add-car">
                            <IconContext.Provider value={{className: "icon-15 blue", title: " " }}>
                                <IoAddCircleSharp />
                            </IconContext.Provider>
                        </Link>
                    </li>
                    <li>
                        <NavLink to="user-cargo">
                            <IconContext.Provider value={{className: "icon-15 green", title: " " }}>
                                <IoCube />
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'> </span>
                        </NavLink>
                        <Link to="/add-cargo">
                            <IconContext.Provider value={{className: "icon-15 blue", title: " " }}>
                                <IoAddCircleSharp />
                            </IconContext.Provider>
                        </Link>
                    </li>
                    <li>
                        <NavLink to="responses">
                            <IconContext.Provider value={{className: "icon-15 green", title: " " }}>
                                <BsFillHandIndexThumbFill />
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'> </span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="in-work">
                            <IconContext.Provider value={{className: "icon-15 green", title: " " }}>
                                <IoBriefcase/>
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'> </span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="tariffs">
                            <IconContext.Provider value={{className: "icon-15 green", title: "" }}>
                                <RiMoneyDollarBoxFill/>
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'></span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="user-patterns">
                            <IconContext.Provider value={{className: "icon-15 green", title: "" }}>
                                <IoNewspaperSharp/>
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'></span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="user-documents">
                            <IconContext.Provider value={{className: "icon-15 green", title: "" }}>
                                <IoDocumentTextSharp />
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'></span>
                        </NavLink>
                        <button type='button' data-bs-toggle="modal" data-bs-target="#docs-patterns">
                            <IconContext.Provider value={{className: "icon-15 blue", title: " " }}>
                                <IoAddCircleSharp />
                            </IconContext.Provider>
                        </button>
                    </li>
                    <li>
                        <button type="button" data-bs-toggle="modal" data-bs-target="#account-exit">
                            <IconContext.Provider value={{className: "icon-15 green", title: "" }}>
                                <IoExitSharp />
                            </IconContext.Provider>
                            <span className='ms-2 ms-xl-3'></span>
                        </button>
                    </li>
                </ul>
            </nav>
        </div>
    )
}